import {ajax} from "../common/ajax";

export function getCategroyMessgaes(columnId,size,success) {
  ajax({
    type: "get",
    url: "http://service.iciba.com/community/inner/column/getList",
    dataType: "jsonp",
    data: {
      "columnId": columnId,
      "size": size
    },
    timeout: 5000,
    before:function(){
    },
    error: function (data) {
      console.log(data);
      alert("请求失败");
    },
    success: data => {
      success ? success(data) : function () {
      };
    }
  });
}

